<template>
  <div class="chatbot-container" @click="closeMenuOnOutsideClick">
    <div class="overlay-menu" :class="{ 'active': isMenuOpen }" @click.stop>
      <button class="close-menu" @click="toggleMenu">X</button>
      <button class="new-chat-button" @click="startNewChat">New Chat</button>
      <div class="chat-history">
        <h3>Chat History</h3>
        <ul>
          <li v-for="chat in chatHistory" 
              :key="chat._id" 
              @click="loadChat(chat)"
              class="chat-history-item">
            {{ formatChatHistory(chat) }}
          </li>
        </ul>
      </div>
      <div class="menu-footer">
        <div class="user-info">
          <div class="user-avatar">{{ userInitials }}</div>
          <div class="user-name">{{ userName }}</div>
        </div>
        <div class="settings-container">
          <button class="settings-button" @click="toggleSettingsMenu">
            <font-awesome-icon icon="cog" />
          </button>
          <div v-if="isSettingsMenuOpen" class="settings-popup">
            <div class="popup-arrow"></div>
            <ul>
              <li><button @click="goToAccountSettings">Account Settings</button></li>
              <li><button>Help & Support</button></li>
              <li><button @click="logout">Logout</button></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="header">
      <div class="header-left">
        <button class="menu-button" @click="toggleMenu"><font-awesome-icon icon="bars" /></button>
      </div>
      <div class="header-center">
        <h2>[의상현] DoctorChat</h2>
      </div>
      <div class="header-right"></div>
    </div>

    <div class="chat-window">
      <div v-if="!currentChat" class="start-chat">
        <div class="start-chat-content">
          <div class="circle"></div>
          <p>Hello! Nice to see you here!</p>
          <p>Please press the "Start chat" button to begin.</p>
          <button @click="startNewChat" class="start-chat-button">Start chat</button>
        </div>
      </div>
      <div v-else class="chat-messages" ref="chatMessages">
        <div v-for="message in currentChat.chat_list" :key="message.date" class="message_container">
          <div :class="['message-header', message.sender]">
            <span v-if="message.sender === 'bot'" class="message-icon"></span>
            <span class="message-name">{{ message.sender === 'user' ? userName : 'DoctorBot' }}</span>
            <span v-if="message.sender === 'user'" class="message-time">{{ formatTime(message.date) }}</span>
          </div>
          <div :class="['message', message.sender]">{{ message.message }}</div>
        </div>
        <div v-if="isLoading" class="message bot">
          <div class="message-content loading">
            <img :src="require('@/assets/loading.gif')" alt="Loading" />
          </div>
        </div>
      </div>
    </div>

    <div v-if="currentChat" class="input-container">
      <input 
        type="text" 
        v-model="newMessage" 
        @keyup.enter="sendMessage" 
        placeholder="Write a message" 
        :disabled="isLoading"
      />
      <button @click="sendMessage" class="btn-send" :disabled="isLoading">
        <font-awesome-icon icon="paper-plane" />
      </button>
    </div>
    <footer class="footer">
      <div>DoctorChatbot V1.</div>
    </footer>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted, nextTick } from 'vue';
import { useRouter } from 'vue-router';
import { auth } from '@/firebase';
import { onAuthStateChanged } from "firebase/auth";
import axios from 'axios';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faBars, faPaperclip, faPaperPlane, faCog } from '@fortawesome/free-solid-svg-icons';
import config from '@/config/config';

library.add(faBars, faPaperclip, faPaperPlane, faCog);

export default {
  setup() {
    const router = useRouter();
    const isChatStarted = ref(false);
    const isLoading = ref(false);
    const userName = ref('');
    const userEmail = ref('');
    const isMenuOpen = ref(false);
    const isSettingsMenuOpen = ref(false);
    const currentChat = ref(null);
    const chatHistory = ref([]);
    const newMessage = ref('');
    const isAuthenticated = ref(false);
    const chatStartDate = ref(null);

    const userInitials = computed(() => userName.value.charAt(0).toUpperCase() || '');

    let unsubscribe;

    onMounted(() => {
      unsubscribe = onAuthStateChanged(auth, async (user) => {
        if (user) {
          isAuthenticated.value = true;
          userName.value = user.displayName || 'User';
          userEmail.value = user.email;
          
          // Check if user has a license
          try {
            const response = await axios.get(`${config.apiBaseUrl}/api/check-license`, {
              params: { email: user.email }
            });
            if (!response.data.license_number) {
              // Redirect to account settings if no license
              router.push('/account-settings');
            } else {
              await loadChatHistory();
            }
          } catch (error) {
            console.error("Error checking license:", error);
            // Handle error (e.g., show error message)
          }
        } else {
          isAuthenticated.value = false;
          router.push('/login');
        }
      });
    });
    
    onUnmounted(() => {
      if (unsubscribe) unsubscribe();
    });

    const toggleMenu = () => {
      isMenuOpen.value = !isMenuOpen.value;
      if (isMenuOpen.value) {
        loadChatHistory();
      }
    };

    const closeMenuOnOutsideClick = (event) => {
      if (isMenuOpen.value && !event.target.closest('.overlay-menu') && !event.target.closest('.menu-button')) {
        isMenuOpen.value = false;
      }
    };

    const goToAccountSettings = () => {
      router.push('/account-settings');
      isSettingsMenuOpen.value = false;
      isMenuOpen.value = false;
    };

    const toggleSettingsMenu = () => {
      isSettingsMenuOpen.value = !isSettingsMenuOpen.value;
    };

    const sendMessage = async () => {
      if (!isAuthenticated.value || newMessage.value.trim() === '' || isLoading.value) {
        return;
      }

      if (!chatStartDate.value) {
        chatStartDate.value = new Date().toISOString();
      }

      const userMessage = {
        sender: 'user',
        date: new Date().toISOString(),
        message: newMessage.value.trim()
      };
      
      if (!currentChat.value) {
        currentChat.value = {
          chat_list: []
        };
      }
      
      currentChat.value.chat_list.push(userMessage);
      newMessage.value = '';
      isLoading.value = true;
      scrollToBottom();

      try {
        const response = await axios.post('https://asia-northeast3-profound-keel-395608.cloudfunctions.net/gpt-doctor-v1', {
          message_user: userMessage.message,
          session_id: userEmail.value,
        });

        const botMessage = {
          sender: 'bot',
          date: new Date().toISOString(),
          message: response.data.message_asst
        };
        isLoading.value = false;
        currentChat.value.chat_list.push(botMessage);
        await saveChatToServer(userMessage, botMessage);
      } catch (error) {

        isLoading.value = false;
        console.error(error);
        
        const errMessage = {
          sender: 'bot',
          date: new Date().toISOString(),
          message: "응답 처리 중 오류가 발생했습니다. 다시 시도해주세요."
        };
        currentChat.value.chat_list.push(errMessage);

        const botMessage = {
          sender: 'bot',
          date: new Date().toISOString(),
          message: error
        };
        await saveChatToServer(userMessage, botMessage);
      } finally {
        scrollToBottom();
      }
    };

    const saveChatToServer = async (userMessage, botMessage) => {
      try {
        await axios.put(`${config.apiBaseUrl}/api/update-chat`, {
          email: userEmail.value,
          chat_date: chatStartDate.value,
          chat_list: [userMessage, botMessage]
        });
      } catch (error) {
        console.error("Error saving chat to server:", error);
      }
    };

    const startNewChat = () => {
      if (!isAuthenticated.value) {
        router.push('/login');
        return;
      }
      currentChat.value = {
        chat_list: []
      };
      chatStartDate.value = null;
      isMenuOpen.value = false;
      scrollToBottom();
    };

    const loadChatHistory = async () => {
      if (!isAuthenticated.value) return;
      try {
        isLoading.value = true;
        const response = await axios.get(`${config.apiBaseUrl}/api/chat-history`, {
          params: { email: userEmail.value }
        });
        chatHistory.value = response.data;
      } catch (error) {
        console.error("Error loading chat history:", error);
        // You might want to show an error message to the user here
      } finally {
        isLoading.value = false;
      }
    };

    const loadChat = (chat) => {
      console.log(chat)
      chatStartDate.value = chat.chat_date;
      currentChat.value = chat;
      isChatStarted.value = true;
      isMenuOpen.value = false;
      scrollToBottom();
    };

    const logout = async () => {
      try {
        await auth.signOut();
        isAuthenticated.value = false;
        userName.value = '';
        userEmail.value = '';
        isSettingsMenuOpen.value = false;
        isChatStarted.value = false;
        currentChat.value = null;
        router.push('/login');
      } catch (error) {
        console.error("Logout error:", error);
      }
    };

    const scrollToBottom = () => {
      nextTick(() => {
        const element = document.querySelector('.chat-messages');
        if (element) {
          element.scrollTop = element.scrollHeight;
        }
      });
    };
    const formatChatHistory = (chat) => {
      const firstMessage = chat.chat_list[0]?.message || 'No messages';
      return `${new Date(chat.chat_date).toLocaleDateString()} - ${firstMessage.substring(0, 30)}...`;
    }

    const formatTime = (dateString) => {
      return new Date(dateString).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    };

    return {
      isChatStarted,
      isLoading,
      userName,
      userEmail,
      isMenuOpen,
      isSettingsMenuOpen,
      currentChat,
      chatHistory,
      newMessage,
      isAuthenticated,
      userInitials,
      toggleMenu,
      toggleSettingsMenu,
      startNewChat,
      sendMessage,
      loadChatHistory,
      formatChatHistory,
      loadChat,
      logout,
      scrollToBottom,
      closeMenuOnOutsideClick,
      goToAccountSettings,
      formatTime,
    };
  }
};
</script>

